.brollopInfo {
  margin: 0px;
  padding-bottom: 550px;
}

.osaButton{
  background-color: #f44336 !important;
  border: none;
  width: 250px;
  height: 70px;
  opacity: 0.5;
}

.selfMadeNavigation {
  position: fixed;
  width: 100%;
  bottom: 0;
  z-index: 9999;
  box-sizing: inherit;
  background-color: #f8f8f8;
  opacity: 0.5;
}

.seldMadeLink {
  color: #777;
  width: 25%;
  float: left;
  padding: 8px 16px;
}

@media only screen and (max-width: 749px) {
  .infoGrid {
    display: grid;
    grid-template-rows: auto auto auto;
    grid-row-gap: 0px;
    padding-top: 50px;
    padding-bottom: 100px;
    padding-left: 20%;
    padding-right: 20%;
    font-size: 18px;
  }
  .innerInfoGrid {
    display: inline;
    display: initial;
  }
  .iconGrid {
    display: grid;
    grid-template-rows: 80px auto;
    grid-row-gap: 0px;
    padding-bottom: 40px;
    align-items: center;
    justify-items: center;
  }

  .homeTextWrapper {
    position: absolute;
    bottom: 25px;
  }

  .homeText {
    font-family: 'gabriola', 'Gabriola', "Raleway", sans-serif !important;
    letter-spacing: 4px;
    font-weight: 100;
    margin: 0;
    font-size: 48px;
    line-height: 1.5;
  }

  .homeNames {
    position: absolute;
    top: 50px;
    font-family: 'gabriola', 'Gabriola', "Raleway", sans-serif !important;
    font-weight: 400;
    font-size: 58px;
    letter-spacing: 4px;
    margin: 10px 0;
    line-height: 1.5;
  }

  .homeTextWrapperDesktop {
    display: none;
  }

  .homeNamesDesktop {
    display: none;
  }
}

@media only screen and (min-width: 750px) {
  .infoGrid {
    display: grid;
    grid-template-rows: auto auto auto;
    grid-row-gap: 50px;
    padding-top: 50px;
    padding-bottom: 100px;
    padding-left: 20%;
    padding-right: 20%;
    font-size: 18px;
  }
  .innerInfoGrid {
    display: grid;
    grid-template-columns: 50% 50%;
  }
  .iconGrid {
    display: grid;
    grid-template-columns: 80px auto;
    align-items: center;
    justify-items: center;
  }
  .homeNames {
    display: none;
  }

  .homeText {
    font-family: 'gabriola', 'Gabriola', "Raleway", sans-serif !important;
    letter-spacing: 4px;
    font-weight: 100;
    margin: 0;
    font-size: 64px;
    line-height: 1.5;
  }

  .homeTextWrapper {
    display: none;
  }
  .homeTextWrapperDesktop {
    position: absolute;
    right: 10%;
  }
  .homeNamesDesktop {
    position: absolute;
    left: 10%;
    font-family: 'gabriola', 'Gabriola', "Raleway", sans-serif !important;
    font-weight: 400;
    font-size: 64px;
    letter-spacing: 4px;
    margin: 10px 0;
    line-height: 1.5;
  }
}

.boendeGrid {
  display: grid;
  grid-template-rows: auto auto auto auto auto;
  padding-left: 20%;
